/* ==============  Container  =============  */

.container {
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  padding: 0 15px;
}

/* ==============  Header  =============  */

.header {
  width: 100%;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1000;

  .container {
    min-height: 100px;
    border-bottom: 1px solid #50536a;
    padding: 30px 0;
    box-sizing: border-box;
  }
}

.header__inner {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
}

.nav,
.header__logo {
  width: 50%;
  font-weight: 700;
  font-size: 13px;
  padding-left: 20px;
}

.nav__list {
  display: flex;
  justify-content: space-between;

  .nav__link {
    text-decoration: none;
    color: #fff;
    text-transform: uppercase;
    opacity: 0.75;
    transition: opacity 0.1s linear;
  }

  .nav__link:hover {
    opacity: 1;
  }

  li {
    list-style: none;
  }
}

/* ==============  Intro  =============  */

.intro {
  display: flex;
  flex-direction: column;
  justify-content: center;
  min-height: 750px;
  background: #31344e url("../../img/content/intro-bg.png.jpg") center no-repeat;
  background-size: cover;
  padding-top: 100px;
  text-align: center;
  color: #fff;

  .container {
    display: flex;
    flex-direction: column;
  }

}

.intro__title {
  max-width: 970px;
  font-size: 65px;
  line-height: 1.1;
  font-family: "Open Sans", sans-serif;
  text-transform: uppercase;
  margin-bottom: 40px;
  font-weight: 700;
}

.intro__subtitle {
  max-width: 970px;
  font-weight: 400;
  margin-bottom: 80px;
  font-size: 22px;
  line-height: 1.5;
  font-family: "Raleway", sans-serif;
}



/* ==============  Button  =============  */

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 14px 40px;
  font-weight: 600;
  font-family: 'Raleway', sans-serif;
  border: 0;
  cursor: pointer;
  border-radius: 2px;
  text-transform: uppercase;
  text-align: center;
  color: #fff;
  text-decoration: none;
  font-size: 13px;
  line-height: 1.6;
  transition: background-color .1s linear;
  margin-bottom: 30px;
}

.btn--red {
  background-color: #e84545;

  &:hover {
    background-color: #dd3434;
  }
}

.btn--long {
  min-width: 280px;
}

/* ==============  Featutes  =============  */

.featutes {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  text-align: center;
  margin-top: 45px;
  margin-bottom: 120px;
}

.featutes__item {
  width: 33.33333%;
  font-size: 14px;
  font-weight: 400;
  margin-top: 75px;
  padding: 0 30px;
}

.featutes__icons {
  padding-bottom: 30px;
}

.features__title {
  text-transform: uppercase;
  font-size: 14px;
  font-weight: 700;
  padding-bottom: 30px;
  color: #2d3033;
}

.featutes__description {
  font-size: 14px;
  line-height: 1.5;

}

/* ==============  Works  =============  */

.works {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;


}

.works__item {
  width: 25%;
  height: 350px;
  position: relative;
  overflow: hidden;
  border-bottom: 1px solid #fff;
  border-right: 1px solid #fff;
}

.works__photo {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 1;
  transform: translate3d(-50%, -50%, 0);
}

.works__content {

  position: absolute;
  z-index: 2;
  background-color: #e84545;
  opacity: 0;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  transition: opacity 0.2s linear;


  .name {
    text-transform: uppercase;
    font-size: 14px;
    font-weight: 700;
    color: #fff;
    display: inline-block;
    margin-bottom: 10px;
  }

  .description {
    font-weight: 400;
    color: #fff;
    display: inline-block;
  }

  &:hover {
    opacity: .9;

  }
}

/* ==============  Team  =============  */

.team {
  margin: 100px 0 70px;
}

.team__list {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
}

.team__item {
  width: 23%;
  margin-bottom: 30px;
}

.member__photo {
  display: block;
  width: 100%;
  height: auto;
  margin-bottom: 20px;
}

.member_name {
  margin-bottom: 8px;
  font-size: 22px;
  color: #2d3033;
}

.member__position {
  margin-bottom: 20px;
  font-size: 13px;
  color: #e84545;
  text-transform: uppercase;

}

.member__description {
  margin-bottom: 20px;
  font-size: 14px;
  line-height: 1.5;
  color: #6c7279;
}


/* ==============  Social-icon =============  */


.social {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
}

.social__item {
  width: 40px;
  height: 40px;
  border: 1px solid #e8ecee;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 4px;
  background-color: #fff;
  transition: background-color 0.1s linear;

  &:hover {
    background-color: #e8ecee;
  }

  &:hover .social__icon {
    fill: #000;
  }
}

.social__icon {
  display: block;
  height: 18px;
  fill: #6c7279;
  transition: fill 0.1s linear;

}

.social--footer {
  background-color: transparent;
  border: 2px solid #eee;
  border-radius: 1px;
  transition: background-color 0.1s linear;

  .social__icon {
    fill: #eee;
    transition: fill 0.1s linear;
  }

  &:hover {
    background-color: #eee;
  }

  &:hover .social__icon {
    fill: #5d6369;
  }
}

/* =============  Reviews  ============  */

.reviews {
  background-color: #53354a;
  overflow: hidden;
}

.reviews__inner {
  display: flex;
  flex-wrap: wrap;
}

.reviews__item {
  width: 50%;
}

.reviews__photo {
  position: relative;
  height: 500px;
}

.reviews__content {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 80px;
}


.reviews__img {
  position: absolute;
  top: 50%;
  right: 0;
  z-index: 1;
  transform: translateY(-50%);
}


.reviews__text {
  font-family: 'Cardo', serif;
  font-style: italic;
  font-size: 36px;
  line-height: 1.17;
  color: #fff;
  margin-bottom: 25px;
}

.reviews__about {
  font-size: 13px;
  font-weight: 700;
  text-transform: uppercase;
  color: #fff;

}

/* =============  Download  ============  */

.download__content {
  width: 100%;
  margin: 90px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.download__title {
  font-size: 28px;
  margin-bottom: 15px;
  color: #2d3033;
  font-weight: 300;
}

.download__text {
  font-size: 13px;
  text-transform: uppercase;
  margin-bottom: 49px;
  color: #2d3033;
  font-weight: 700;
}


/* =============  Footer  ============  */


.footer {
  background-color: #3a3e64;
}

.footer__inner {
  display: flex;
  margin: 70px 0;
  flex-wrap: wrap;

}

.footer__item {
  width: 33.33333%;
  text-align: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 15px;

  .footer__address {
    font-style: normal;
    line-height: 1.6;
  }
}

.footer__title {
  margin-bottom: 20px;
  font-size: 14px;
  text-transform: uppercase;
  color: #eee;
  font-weight: 700;
}

.footer__text {
  font-size: 14px;
  line-height: 1.6;
  color: rgba($color: #fff, $alpha: .5);
  font-weight: 400;


}


/* =============  Copyright  ============  */


.copyright {
  padding: 25px 0;
  background-color: #313454;
  text-align: center;

  .copyright__inner {
    width: 100%;
    text-align: center;
    color: #808080;
    line-height: 1.5;

    span {
      color: #fff;
    }
  }
}

/* ==============  Burger  =============  */

.burger {
  display: none;
  padding: 9px 2px;

  background: none;
  border: 0;
  cursor: pointer;

  padding-right: 15px;
}

.burger__item {
  display: block;
  width: 30px;
  height: 3px;
  background-color: #fff;
  font-size: 0;
  color: transparent;
  position: relative;
}

.burger__item::before,
.burger__item::after {
  content: "";
  display: block;
  width: 30px;
  height: 3px;
  background-color: #fff;
  position: absolute;
  left: 0;
  z-index: 1;
}

.burger__item::before {
  top: -8px;
}

.burger__item::after {
  bottom: -8px;
}



/* ==============  Media  =============  */
@media (max-width: 1300px) {


  /*====== Works ======*/
  .works__item {
    width: 50%;
  }

}



@media (max-width: 991px) {

  /*====== Nav ======*/
  .nav {
    display: none;
  }


  /*====== Burger ======*/
  .burger {
    display: block;
  }


  /*====== Intro ======*/
  .intro__title {
    font-size: 40px;
  }


  /*====== Featutes ======*/
  .featutes__item {
    width: 50%;
  }


  /*====== Team ======*/
  .team__item {
    width: 46%;
    margin-bottom: 30px;
  }



  /*====== Reviews ======*/
  .reviews__inner {

    justify-content: space-between;
  }

  .reviews__img {
    right: 0;
  }

  .reviews__content {
    padding: 40px;
  }

  .reviews__item {
    width: 100%;
  }
}



@media (max-width: 767px) {

  /*===== intro ======*/
  .intro {
    height: auto;
  }

  .intro__title {
    font-size: 30px;
  }

  .intro__subtitle {
    font-size: 16px;
    margin-bottom: 30px;
  }


  /*====== Featutes ======*/
  .featutes {
    margin-top: 0px;
    margin-bottom: 75px;
  }

  .featutes__item {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
  }



  /*====== Works ======*/
  .works__item {
    width: 100%;
    height: 300px;
  }


  /*====== Team ======*/
  .team__item {
    width: 100%;
    margin-bottom: 30px;
  }


  /*====== Reviews ======*/
  .reviews__text {
    font-size: 25px;
  }

  /*====== Btn ======*/
  .download__content {
    text-align: center;
  }


  .btn--long {
    min-width: 260px;
  }


  /*====== Download ======*/
  .download__content {
    margin: 50px 0;
  }

  .download__title {
    font-size: 24px;
  }



  .footer__inner {
    margin: 30px 0;
  }

  .footer__item {
    width: 100%;
    padding-left: 0;
    padding-right: 0;
    margin-bottom: 30px;
  }

  .footer__item:last-child {
    margin-bottom: 0;
  }
}


@media (max-width: 414px) {



  /*====== Reviews ======*/

  .reviews__img {
    right: -100px;
  }
}